<template>
  <HeaderEl
    class="header"
    :name="sceneName">
  </HeaderEl>

  <!-- 본문 -->
  <main class="wrap" @onScroll="onScroll">
    <h2>Vue 컴포넌트</h2>
    <div class="sortUi">
      <button v-for="a in b" :key="a" type="button">{{ a }}</button>
    </div>

    <button type="button" @click="isShow = !isShow">test</button>
    <TransitionGroup name="list">
      <ul class="child" v-if="isShow">
        <li>item 1</li>
        <li>item 2</li>
        <li>item 3</li>
        <li>item 4</li>
      </ul>
    </TransitionGroup>

    <section class="uiGuide">
      <article class="uiBox_icofont" @click="icofontRadius">
        <h3 class="just_mean">아이콘 Font</h3>
      </article>
      <ul class="defineBox theme:icofont testarea">
        <li class="construct">
          <label>
            <span><small>min</small>44px</span>
          </label>
          <p>웹 폰트</p>
          <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Libero, accusamus. Aliquid dolores obcaecati perferendis nostrum ut. Amet.</p>
        </li>
        <li class="example">
          <!-- open 속성: code 표시 -->
          <details>
            <summary @click="codeOpen(e); isSummary = !isSummary">.code { … }<button></button></summary>
            <TransitionGroup name="list2">
              <code class="code child" v-if="isSummary">
                (name, count, character) {<br>
                &nbsp;&nbsp;&nbsp;&nbsp;this.name = name;<br>
                &nbsp;&nbsp;&nbsp;&nbsp;this.count = count;
              </code>
            </TransitionGroup>
          </details>
        </li>
      </ul>
      <article class="uiBox_color">
        <h3>컬러</h3> 
      </article>
      <ul class="defineBox theme:color" style="background-color: yellowgreen;"></ul>
    </section>
  </main>
</template>

<script>
import HeaderEl from './common/Header.vue';

export default {
  // el: '#test',
  name: 'UiCompo',
  components: {
    HeaderEl,
  },
  data: () => ({
    sceneName: 'UI 컴포넌트',
    isShow: false,
    isSummary: false,
    b: ['웹폰트', '인풋', '컬러'],
  }),
  beforeUnmount() {
    window.removeEventListener("scroll", this.onScroll, true)
  },
  mounted() {
    this.codeOpen();
    this.icofontRadius();
    // this.slideUpDown();
    this.onScroll();
    // window.addEventListener("click", this.codeOpen, this.icofontRadius, this.slideUpDown);
    window.addEventListener("click", this.codeOpen, this.icofontRadius);
    window.addEventListener("scroll", this.onScroll, true);
  },
  methods: {
    // slideUpDown(e) {
    //   if(e) {
    //     this.isShow = true;
    //   }
    // },
    icofontRadius(e) {
      if(e) {
        let current = e.currentTarget,
            nextElement = current.nextElementSibling;
            console.log(nextElement.classList.contains('on'));
        if(nextElement.classList.contains('on') === false) {
          nextElement.classList.add('on');
          current.classList.add('borderBottomrightRadius');
        } else {
          nextElement.classList.remove('on');
          current.classList.remove('borderBottomrightRadius');
        }
      }
    },
    codeOpen(e) {
      if(e) {
        // 방법: target;   
        // 태그: summary @click="함수명(e)"
        // (중복으로 호출됨), ancestor 로 open속성 확인
        let target = e.target,
            ancestor = target.closest('details'); 
        // console.log(closest);    최상위 요소가 details 아니라면 null 뜸
        if(ancestor != null) {
          // console.log(`부모 요소 open 속성: ${ancestor.open}`);
          if(ancestor.open === false) { 
            if(target.tagName == 'BUTTON') {
              target.closest('summary').classList.add('openDetails');
              ancestor.open = true;
            } else { target.classList.add('openDetails'); }
          } 
          else { 
            if(target.tagName == 'BUTTON') {
              target.closest('summary').classList.remove('openDetails');
              ancestor.open = false;
            } else { target.classList.remove('openDetails'); }
          }
        } 
        else {
          return;
        }
      }
    },
    onScroll() {
      // console.log('scrolled');
      // this.windowTop = e.target.scrollTop;

      const selector = document.querySelector('.wrap'),
            header = document.querySelector('.header');
      
      this.windowTop = window.top.scrollY;
      this.scrolledTop = selector.scrollTop;
      console.log(this.windowTop, this.scrolledTop);

      if(this.scrolledTop > 0) {
        header.classList.add('scrolled');
      } else {
        header.classList.remove('scrolled');
      }
    }
  }
}
</script>

